.screens {
  max-width: 2560px;
  margin: 0 auto;
  max-height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  scroll-snap-type: y mandatory;
}

.screen {
  scroll-snap-align: start;
}

.screen-promo {
  height: 100vh;
  max-height: 1366px;
  overflow: hidden;
  position: relative;
  z-index: 1;
  background-color: #B9B1BB;
  padding-top: 150px;
  padding-top: min(15vh, 150px);
  padding-left: 180px;
  padding-left: min(9.375vw, 180px);
}
.screen-promo-content {
  color: #2D2835;
  max-width: 560px;
}
.screen-promo-logo {
  margin-bottom: 50px;
}
.screen-promo-label {
  font-size: 48px;
  font-size: 3rem;
  font-weight: 500;
  line-height: 1.2;
  font-family: "Accia Flare";
}
.screen-promo-bg {
  position: absolute;
  z-index: -2;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  object-fit: cover;
  object-position: center;
}
.screen-promo-bottles {
  position: absolute;
  z-index: -1;
  right: 0;
  bottom: 0;
  max-height: 100%;
  max-width: 85%;
  display: block;
}
@media (min-aspect-ratio: 4/3) {
  .screen-promo-bottles {
    max-width: 75%;
  }
}
@media (orientation: portrait) {
  .screen-promo-bottles {
    max-width: 100%;
  }
}
.screen-promo-appeal {
  max-width: 156px;
  position: absolute;
  bottom: 0px;
  left: 50%;
  transform: translate(-50%, 50%);
  cursor: pointer;
}
.screen-promo-appeal-rotate {
  animation: rotate 12s linear infinite;
  transition: opacity 0.35s;
}
.screen-promo-appeal-arrow {
  position: absolute;
  left: 0;
  right: 0;
  top: 30px;
  margin: 0 auto;
  transition: transform 0.35s;
}
.screen-promo-appeal:hover .screen-promo-appeal-arrow {
  transform: translateY(3px);
}
.screen-promo-appeal:hover .screen-promo-appeal-rotate {
  opacity: 0.6;
}
@media (min-width: 1921px) {
  .screen-promo-content {
    max-width: 718px;
  }
  .screen-promo-label {
    font-size: 62px;
    font-size: 3.875rem;
  }
  .screen-promo-bottles {
    height: 85%;
  }
}
