.screen-alert {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  right: 5px;
  height: calc(100% - 40px);
  z-index: 1;
}
@media (orientation: portrait), (max-width: 768px) {
  .screen-alert {
    visibility: hidden;
    display: none;
  }
}

