.benefits-slider-nav {
  position: absolute;
  z-index: 1;
  left: 50%;
  width: 50%;
  bottom: 70px;
  bottom: min(7vh, 70px);
  padding: 0 100px 0 40px;
}
.benefits-slider-nav-inner {
  max-width: 500px;
  margin: 0 auto;
}
.benefits-slider-arrow {
  width: 30px;
  height: 60px;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
  transition: opacity 0.2s;
}
.benefits-slider-arrow:not(.swiper-button-disabled):hover {
  opacity: 0.75;
}
.benefits-slider-arrow.swiper-button-disabled {
  cursor: default;
}
.benefits-slider-prev {
  background: url(../images/arrow-prev.svg) center no-repeat;
  background-size: contain;
}
.benefits-slider-next {
  background: url(../images/arrow-next.svg) center no-repeat;
  background-size: contain;
}
.benefits-slider-previews {
  display: inline-block;
  vertical-align: middle;
  margin: 0px 20px;
  font-size: 0;
  white-space: nowrap;
  overflow-y: hidden;
  overflow-x: auto;
}
.benefits-slider-previews::-webkit-scrollbar {
  width: 0;
  height: 0;
  display: none;
}
.benefits-slider-preview {
  display: inline-block;
  vertical-align: middle;
  width: 60px;
  height: 60px;
  position: relative;
  border-radius: 30px;
  overflow: hidden;
  margin: 0px 6px 0px;
  cursor: pointer;
  opacity: 0.65;
  transition: opacity 0.2s;
}
.benefits-slider-preview.active, .benefits-slider-preview:hover {
  opacity: 1;
}
.benefits-slider-preview img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
@media (max-width: 1680px) {
  .benefits-slider-nav {
    padding-left: 80px;
